.container-fluid {
  background: #fff;
  padding: 10px 0px 10px 0px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px rgb(29, 240, 57);
  max-width: 50%;
  margin-top: 19.5%;
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.row {
  padding: 0px !important;
  margin: auto;
  width: 100%;
  height: 100%;
  justify-content: center;
}
